import React from 'react';
import styles from './icon.module.css';

const Icon = ({ name, icon, href }) => (
    <div className={ styles.iconWrap }>
        <a
            href={ href }
            rel="noopener noreferrer"
            target="_blank"
        >
            <svg className={ styles.icon } viewBox={icon.viewBox}>
                <title>{name}</title>
                <path d={icon.path} />
            </svg>
    </a>
  </div>
);

export default Icon;