import React from "react"
import ICONS from "./icons"
import Icon from "./icon"
import socialStyle from "./sociallinks.module.css"

const getContactHref = (name, contact) => {
    let href;
  
    switch (name) {
      case 'twitter':
        href = `https://www.twitter.com/${contact}`;
        break;
      case 'github':
        href = `https://github.com/${contact}`;
        break;
      case 'vkontakte':
        href = `https://vk.com/${contact}`;
        break;
      case 'telegram':
        href = `https://t.me/${contact}`;
        break;
      case 'email':
        href = `mailto:${contact}`;
        break;
      case 'linkedin':
        href = `https://www.linkedin.com/in/${contact}`;
        break;
      case 'instagram':
        href = `https://www.instagram.com/${contact}`;
        break;
      case 'line':
        href = `line://ti/p/${contact}`;
        break;
      case 'facebook':
        href = `https://www.facebook.com/${contact}`;
        break;
      case 'gitlab':
        href = `https://www.gitlab.com/${contact}`;
        break;
      case 'weibo':
        href = `https://www.weibo.com/${contact}`;
        break;
      default:
        href = contact;
        break;
    }
  
    return href;
}

const getIcon = (name) => {
    let icon;
  
    switch (name) {
      case 'twitter':
        icon = ICONS.TWITTER;
        break;
      case 'github':
        icon = ICONS.GITHUB;
        break;
      case 'vkontakte':
        icon = ICONS.VKONTAKTE;
        break;
      case 'telegram':
        icon = ICONS.TELEGRAM;
        break;
      case 'email':
        icon = ICONS.EMAIL;
        break;
      case 'rss':
        icon = ICONS.RSS;
        break;
      case 'linkedin':
        icon = ICONS.LINKEDIN;
        break;
      case 'instagram':
        icon = ICONS.INSTAGRAM;
        break;
      case 'line':
        icon = ICONS.LINE;
        break;
      case 'facebook':
        icon = ICONS.FACEBOOK;
        break;
      case 'gitlab':
        icon = ICONS.GITLAB;
        break;
      case 'weibo':
        icon = ICONS.WEIBO;
        break;
      default:
        icon = {};
        break;
    }
  
    return icon;
}

const SocialLinksItem = ({ name, contact }) => {
    let c = 0;

    return (
        <li>
            <Icon name={ name } icon={ getIcon(name) } href={ getContactHref(name, contact) } key={ c++ } />
        </li>
    )
}

const SocialLinksInternal = ({ socialProfiles }) => {
    return (
        <>
            {
                socialProfiles.map(item => <SocialLinksItem name={ item.name } contact={ item.contact }/>)
            }
        </>
    )
}

const SocialLinks = () => {

    const socialProfiles = [
        {
            name: 'facebook',
            contact: 'dockovpn'
        },
        {
            name: 'twitter',
            contact: 'dockovpn'
        },
        {
            name: 'instagram',
            contact: 'dockovpn'
        },
        /*{
            name: 'linkedin',
            contact: 'scalaguru'
        },*/
        {
            name: 'email',
            contact: 'array.shift@yahoo.com'
        }
    ]

    return (
        <div className={ socialStyle.socialLinks }>
            <ul>
                <SocialLinksInternal socialProfiles={ socialProfiles }/>
            </ul>
        </div>
    )
}

export default SocialLinks