import React from "react"
import { useTranslation } from 'gatsby-plugin-react-i18next'

const MailChimpSubscribe = ({ desktopOnly }) => {
    const { t } = useTranslation()
    const mailChimpHtml = 
`<!-- Begin Mailchimp Signup Form -->
<div id="mc_embed_signup">
<form action="https://dockovpn.us6.list-manage.com/subscribe/post?u=73b04b7d3c2a6798b033d9f45&amp;id=1a87251de9" method="post" id="mc-embedded-subscribe-form" name="mc-embedded-subscribe-form" class="validate" target="_blank" novalidate>
    <div id="mc_embed_signup_scroll">
	<label for="mce-EMAIL">${ t("emailSubscribe.caption") }</label>
	<input type="email" value="" name="EMAIL" class="email" id="mce-EMAIL" placeholder="${ t("emailSubscribe.emailPlaceholder") }" required>
    <!-- real people should not fill this in and expect good things - do not remove this or risk form bot signups-->
    <div style="position: absolute; left: -5000px;" aria-hidden="true"><input type="text" name="b_73b04b7d3c2a6798b033d9f45_1a87251de9" tabindex="-1" value=""></div>
    <div class="clear"><input type="submit" value="${ t("emailSubscribe.sendBtn") }" name="subscribe" id="mc-embedded-subscribe" class="button"></div>
    </div>
</form>
</div>
<!--End mc_embed_signup-->`

    const desktop = desktopOnly || false
    const classNames = desktop ? "desktop" : "" 

    return (<div className={ classNames } dangerouslySetInnerHTML={{__html: mailChimpHtml}}></div>)
}

export default MailChimpSubscribe